import { Body, Controller, Post } from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiConsumes,
  ApiInternalServerErrorResponse,
  ApiOkResponse,
  ApiOperation,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';

import { ProjectScenario } from '../../../application/scenario/project';
import { CustomLogger } from '../../../utility';
import { ProjectCreateRequest } from '../request';
import { ProjectResponse } from '../response';

@ApiTags('Projects')
@Controller('projects')
export class ProjectCreateController {
  constructor(
    readonly logger: CustomLogger,
    readonly scenario: ProjectScenario,
  ) {
    this.logger.setContext(ProjectCreateController.name);
  }
  @Post()
  @ApiConsumes('application/json')
  @ApiOperation({
    description: 'Create Project',
  })
  @ApiOkResponse({ type: ProjectResponse })
  @ApiBadRequestResponse({
    description: 'リクエスト内容が不正',
  })
  @ApiInternalServerErrorResponse({
    description: 'サーバーエラー',
  })
  @ApiUnauthorizedResponse({
    description: '認証エラー',
  })
  async create(@Body() body: ProjectCreateRequest): Promise<ProjectResponse> {
    return await this.scenario.pushProject(body);
  }
}
